
  @font-face {
    font-family: amatic;
    src: url('../../styles/fonts/new/Alef-Regular.ttf');
  }
  @font-face {
    font-family: DancingScript;
    src: url('../../styles/fonts/numFont/DancingScript-VariableFont_wght.ttf');
  }

  @import url('https://fonts.googleapis.com/css?family=Aguafina+Script');
#text2{
    font-family: "Aguafina Script", cursive;
    text-align: center;
    font-size: 25px;
    // text-shadow: rgb(83, 83, 83) 2px 2px 2px;
}

.calendar-admin-container{
    width: 100%;
    height: 100%;
    background-color: #f8ebed;
}
.loader-container{
    height: 300px;
}

.table-container {
    overflow-y: auto;
    height: 600px;
  }
  .days-header-container{
      height: 44px;
      border: none;
      background-color: #273650;
      color: white;
      width: 100%;
    }
    
    .dayes-name-container{
        width: 59.2px;
    }

  .td-hours{
    border: none;
    border-bottom: 1px solid #a9a9b05b;
    font-weight: 600;
    background-color: white;
    color: #273650; 
  }

  table {
    border-collapse: collapse;
    width: 100%;
  }
  th,
  td {
    height: 80px;
    width: 14%;
    padding: 10px 0px;
    .occupied-cell-content{
        width: 100%;
        height:100% ;
        overflow-y: scroll;  
        overflow-x: hidden;
      }
  }

 .occupied-cell{
    border: none;
    border-bottom: 1px solid #a9a9b05b;
    border-radius: 7px;
    background-color: white;
    color: #273650; 
    word-spacing: 60.5px;
 }

.small-font{
    font-size: 11px;
    letter-spacing: 1.5px;
}

.available-cell{
    border: none;
    border-bottom: 1px solid #a9a9b02b;
    border-left: 1px solid #a9a9b02b;
    border-radius: 7px;
    background-color:#ffc0cb00;
}

.even-event-0{
    background-color:#0066ff2b;
}
.even-event-1{
    background-color:#ffa50045;
}
.even-event-2{
    background-color:#f0d3ff;
}
.odd-event-0{
    background-color:#3629ff4a;
}
.odd-event-1{
    background-color:#ff297c38;
}
.odd-event-2{
    background-color:#00ff7812;
}
.header-app{
    padding: 5px 20px;
}
.weekly-dates-container{
    height: 47px;
    font-size: 17px;
    font-weight: bold;
}
.fa-calendar-week{
    font-size: 20px;
}
.weekly-dates-text{
    width: 60%;
    font-size: 15px;
    font-family: DancingScript;  
}

.fa-plus{
    font-size: 11px;
    color: #0000003d;
}

.daily-num{
    font-size: 12px;
    text-align: left;
    padding-left: 3px;
}

.daily-name{
    font-family: amatic;
    font-size: 15px;
    letter-spacing: 1px;
    text-align: right;
    padding-right: 3px;
}
.month-name{
    padding: 10px;
    transform: rotate(-25deg);
    font-size: 13px;
}

.calendar-footer{
    width: 100%;
    height: 40px;
    background-color: #bbbec38c;
    .footer-hours{
        font-weight: 600;
        color: #273650; 
    }
}

