.apointments-modal{
    .header-in-form-modal{
        height: 50px;
        width: 100%;
        background-color: #273650;
        border-radius: 24px 24px 0 0;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 16px;
    }
    position: absolute;
    width: 80%;
    height: 80%;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    z-index: 2;
    background-color: #ffecf2;
    border-radius: 24px;
    button[disabled]{
        color:rgba(255, 255, 255, 0.616);
    }
}

.modal-screen{
        background: rgba(0, 0, 0, 0.5);
        position: fixed;
        top: 0;
        left: 0;
        height: 100vh;
        width: 100vw;
        justify-content: center;
        z-index: 1;
}

//   I phone 5
@media screen and (max-height: 570px) {
    .table-container {
    height:475px;
      }
      .dayes-name-container{
        width: 44.2px;
    }
  }

//   I phone 6-8 
@media screen and (max-height: 670px) and (min-height: 570px){
    .table-container {
        height:571px;
    }
  }
//   I phone 6-8 plus 
@media screen and (max-height: 810px) and (min-height: 730px){
    .table-container {
        height:634px;
    }
  }

//   I phone x
@media screen and (min-height: 810px) {
    .table-container {
        height:719px;
    }
    .dayes-name-container{
        width: 53.2px;
    }
  }

  //   Ipad
@media screen and (min-height: 1000px) {
    .table-container {
        height:929px;
    }
  }
