.time-slot-lists-container{
    max-height: 340px;
    margin-top: 1.3rem;
    .loaderContainer{
        overflow-y: hidden;
        height: 200px;
    }
}
// MATERIAL UI CLASSES
.MuiDialogActions-root{
    justify-content: space-between!important;
}


.calendar-picker-container{
    margin-bottom: 85px;
}

.date-picker-title{
    font-size: 1rem;
    margin-bottom: 10px;
}

.picker-red-title{
  color: #680505;
  font-size: 1rem;
  margin-bottom: 10px;
}

//   I phone 5
@media screen and (max-height: 570px) {
    .time-slot-lists-container{
        // width: 390px;
        max-height: 181px;
    }
  }

//   I phone 6-8 
@media screen and (max-height: 670px) and (min-height: 570px){
    .time-slot-lists-container{
        // width: 390px;
        max-height: 269px;
    }
  }
//   I phone 6-8 plus 
@media screen and (max-height: 810px) and (min-height: 730px){
    .time-slot-lists-container{
        // width: 390px;
        max-height: 362px;
    }
  }

//   I phone x
@media screen and (min-height: 810px) {
    .time-slot-lists-container{
        // width: 390px;
        max-height: 446px;
    }
  }

  //   Ipad
@media screen and (min-height: 1000px) {
    .time-slot-lists-container{
        // width: 390px;
        max-height: 690px;
    }
  }